<script>
import { ref } from 'vue';
import { useAxios } from '@/composables/useAxios';

export default {
  name: 'ProfileComponent',
  
  data() {
    return {
      accountData: {
        avatarImg: "",
      },
      refInputEl: null,
      isConfirmDialogOpen: false,
      accountDataLocal: null,
      isAccountDeactivated: false,
      validateAccountDeactivation: [v => !!v || 'Please confirm account deactivation'],
      profile: {}
    };
  },
  async created() {
    this.accountDataLocal = structuredClone(this.accountData);
    await this.fetchProfile();
  },
  methods: {
    async fetchProfile() {
      try {
        const response = await useAxios().get('profile');
        this.profile = response[0]; // Assuming the profile data is in `response.data`
      } catch (error) {
        console.error('Error fetching profile:', error);
      }
    },
    async saveProfileData()
    {
      const data = this.profile;
      delete data.image;
      try{
        const response = await useAxios().put('profile/' + this.profile.id, data);
        this.profile = response;
        const { user, ...userInfo } = response;
        this.$store.state.userInfo.profile = userInfo;
        this.$store.dispatch("setAlert", {
            type: "success",
            title: "Profile Edit",
            text: "Your profile data has been updated successfully",
          });
      }catch{
        this.$store.dispatch("setAlert", {
            type: "Error",
            title: "Profile Edit",
            text: "an error occured while saving profile data",
          });
      }

    },

    resetForm() {
      this.accountDataLocal = structuredClone(this.accountData);
    },

    changeAvatar(event) {
      const fileReader = new FileReader();
      const files = event.target.files;
      if (files && files.length) {
        fileReader.readAsDataURL(files[0]);
        fileReader.onload = () => {
          if (typeof fileReader.result === 'string') {
            this.accountDataLocal.avatarImg = fileReader.result;
          }
        };
      }
    },

    resetAvatar() {
      this.accountDataLocal.avatarImg = this.accountData.avatarImg;
    }
  }
};
</script>


<template>
  <VRow class="p-10" v-if="profile.user">
    <VCol cols="12">
      <VCard title="Profile Details" rounded="xl" class="p-5">
        <VCardText class="d-flex">
          <!-- ðŸ‘‰ Avatar -->
          <VAvatar
            rounded
            size="100"
            class="me-6"
            :image="accountDataLocal.avatarImg"
          />

          <!-- ðŸ‘‰ Upload Photo -->
          <form class="d-flex flex-column justify-center gap-4">
            <div class="d-flex flex-wrap gap-2">
              <VBtn
                color="primary"
                @click="refInputEl?.click()"
              >
                <VIcon
                  icon="tabler-cloud-upload"
                  class="d-sm-none"
                />
                <span class="d-none d-sm-block">Upload new photo</span>
              </VBtn>

              <input
                ref="refInputEl"
                type="file"
                name="file"
                accept=".jpeg,.png,.jpg,GIF"
                hidden
                @input="changeAvatar"
              >

              <VBtn
                type="reset"
                color="secondary"
                variant="tonal"
                @click="resetAvatar"
              >
                <span class="d-none d-sm-block">Reset</span>
                <VIcon
                  icon="tabler-refresh"
                  class="d-sm-none"
                />
              </VBtn>
            </div>

            <p class="text-body-1 mb-0">
              Allowed JPG, GIF or PNG. Max size of 800K
            </p>
          </form>
        </VCardText>

        <VDivider />

        <VCardText class="pt-2">
          <!-- ðŸ‘‰ Form -->
          <VForm class="mt-6">
            <VRow>
              <!-- ðŸ‘‰ First Name -->
              <VCol
                md="6"
                cols="12"
              >
                <VTextField
                  prepend-inner-icon="mdi-account"
                  v-model="profile.user.first_name"
                  label="First Name"
                />
              </VCol>

              <!-- ðŸ‘‰ Last Name -->
              <VCol
                md="6"
                cols="12"
              >
                <VTextField
                  prepend-inner-icon="mdi-account"
                  v-model="profile.user.last_name"
                  label="Last Name"
                />
              </VCol>

              <!-- ðŸ‘‰ Email -->
              <VCol
                cols="12"
                md="6"
              >
                <VTextField
                  prepend-inner-icon="mdi-email"
                  v-model="profile.user.email"
                  label="E-mail"
                  type="email"
                />
              </VCol>

              <!-- ðŸ‘‰ Phone -->
              <VCol
                cols="12"
                md="6"
              >
                <VTextField
                  prepend-inner-icon="mdi-phone"
                  v-model="profile.phone"
                  label="Phone Number"
                />
              </VCol>

              <!-- ðŸ‘‰ Address -->
              <VCol
                cols="12"
                md="6"
              >
                <VTextField
                  prepend-inner-icon="mdi-home-map-marker"
                  v-model="profile.address"
                  label="Address"
                />
              </VCol>

              <!-- ðŸ‘‰ City -->
              <VCol
                cols="12"
                md="6"
              >
                <VTextField
                  prepend-inner-icon="mdi-city"
                  v-model="profile.city"
                  label="City"
                />
              </VCol>

              <!-- ðŸ‘‰ State -->
              <VCol
                cols="12"
                md="6"
              >
                <VTextField
                prepend-inner-icon="mdi-map-outline"
                  v-model="profile.state"
                  label="State"
                />
              </VCol>

              <!-- ðŸ‘‰ Country -->
              <VCol
              
                cols="12"
                md="6"
              >
              <VTextField
                  prepend-inner-icon="mdi-flag"
                  v-model="profile.country"
                  label="Country"
                />
              </VCol>

              <!-- ðŸ‘‰ Zip Code -->
              <VCol
                cols="12"
                md="6"
              >
                <VTextField
                  prepend-inner-icon="mdi-numeric"
                  v-model="profile.postal_code"
                  label="Zip Code"
                />
              </VCol>


              <!-- ðŸ‘‰ Form Actions -->
              <VCol
                cols="12"
                class="d-flex flex-wrap gap-4"
              >

                <VBtn @click.prevent="saveProfileData()">Save changes</VBtn>
                
              </VCol>
            </VRow>
          </VForm>
        </VCardText>
      </VCard>
    </VCol>

    <VCol cols="12">
      <!-- ðŸ‘‰ Delete Account -->
      <VCard title="Delete Account">
        <VCardText>
          <!-- ðŸ‘‰ Checkbox and Button  -->
          <div>
            <VCheckbox
              v-model="isAccountDeactivated"
              :rules="validateAccountDeactivation"
              label="I confirm my account deactivation"
            />
          </div>

          <VBtn
            :disabled="!isAccountDeactivated"
            color="error"
            class="mt-3"
            @click="isConfirmDialogOpen = true"
          >
            Deactivate Account
          </VBtn>
        </VCardText>
      </VCard>
    </VCol>
  </VRow>

  <!-- Confirm Dialog -->
  <ConfirmDialog
    v-model:isDialogVisible="isConfirmDialogOpen"
    confirmation-question="Are you sure you want to deactivate your account?"
    confirm-title="Deactivated!"
    confirm-msg="Your account has been deactivated successfully."
    cancel-title="Cancelled"
    cancel-msg="Account Deactivation Cancelled!"
  />
</template>
